import './App.css';
import { useEffect, useRef, useState } from 'react'
import phone from './assets/phone.png'
import logo from './assets/logo.png'
import instagram from './assets/instagram.png'
import twitter from './assets/twitter.png'
import facebook from './assets/facebook.png'
import { strings, lang } from './misc/strings'
import Loader from './components/Loader'

import firebase from 'firebase/app'
import 'firebase/database'
import {auth, db} from './misc/firebase'

function App() {
	const uid = useRef(null)
	const [lan, setLan] = useState('en')
	const [email, setEmail] = useState('');
	const [loading, setLoading] = useState(false)
	const [subscribed, setSubscribed] = useState(false);
	const [errors, setErrors] = useState(false)
	useEffect(() => {
		auth.onAuthStateChanged(u => {
			if(u) {
				console.log('opened, ',u.uid)
				uid.current = u.uid
				db.ref('landingUsers').child(u.uid).child('opened').push(firebase.database.ServerValue.TIMESTAMP)
				db.ref('landingUsers').child(u.uid).child('langs').orderByKey().limitToLast(1).once('value').then(snap => {
					snap.forEach(i => setLan(i.child('lan').val()))
				})
			} else {
				auth.signInAnonymously()
			}
		})
	}, [])

	const subscribe = () => {
		setLoading(true)

		db.ref('landingEmails').push({email, uid: uid.current}).then(() => {
			setSubscribed(true)
			setLoading(false)
			setTimeout(() => {
				setSubscribed(false)
			}, 2000)
			setEmail('')
		})
	}

	const entered = (v) => {
		let e = v.target.value
		setEmail(e)
		if(e.length && !email.match(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)$/)) {
			setErrors("Please enter a valid email address")
		}
		else {
			setErrors(false)
		}
	}


	const changeLang = (toLang) => {
		setLan(toLang)
		db.ref('landingUsers').child(uid.current).child('langs')
			.push({lan: toLang, time: firebase.database.ServerValue.TIMESTAMP, uid: uid.current})
	}

	const openSm = (sm) => {
		db.ref('landingUsers').child(uid.current).child('sm')
			.push({sm, time: firebase.database.ServerValue.TIMESTAMP, uid: uid.current});
	}

  return (
		<>
			<div className="App">
				<div className="row-item">
					{/* <div className="title-row">
					</div> */}
					<h1>{strings.explore_kerala[lan]}</h1>
					<h2>{strings.enjoy_trips[lan]}</h2>
					<div className="border" />
					<h3 className="mt-3">{strings.for_updates[lan]}</h3>
					<div className="fields">
						<input disabled={loading || subscribed} value={email} onChange={entered} placeholder={strings.your_email[lan]} />
						{/* <a onClick={() => alert('jdj')} className="subscribe big-only" href="#">{strings.subscribe[lan]}</a> */}
						{loading?
							<div className="big-only" style={{alignSelf: 'center'}}>
								<Loader className="big-only" />
							</div>
						:
							<button onClick={() => subscribe()} disabled={subscribed || errors || !email.length} className={`subscribe big-only ${subscribed?'sb-done':errors || !email.length ?'sb-disabled':''}`}>{subscribed?'Done!':strings.subscribe[lan]}</button>
						}
					</div>
					<button onClick={() => subscribe()} disabled={loading || subscribed || errors || !email.length} className={`subscribe-button small-only ${subscribed?'sb-btn-done':loading || errors || !email.length?'sb-btn-disabled':''}`}>{
						loading?
							<Loader color="#fff" />
						:subscribed?
							"Done!"
						:
							strings.subscribe[lan]
					}</button>
					<h4 className="mt-5">{strings.coming_soon[lan]}</h4>
				</div>
				<div className="scr-outer">
					<img src={phone} className="screenshots" alt="Screenshots" />
					<div className="imgShadow" />
				</div>
				<div className="header">
					<img src={logo} className="logo logo-main" />
					<div className="logoText">Thrills of Kerala</div>
					<div className="f1" />
					{['ml','en'].map((i,ii) => (
						<div onClick={() => changeLang(i)} key={ii} className={"lan "+(i!==lan?"in":"")+"activelan big-only"}>{lang[i]}</div>
					))}
					<div className="hdiv big-only" />
					<div style={{marginRight: '5px'}} className="big-only">Support us on:</div>
					<a href='https://www.facebook.com/thrillsofkerala/' onClick={() => openSm('fb')} target="_blank" className="sm-ic-outer logo big-only" style={{backgroundColor: '#4267B2'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+facebook+')'}} />
					</a>
					<a href='https://twitter.com/thrillsofkerala/' onClick={() => openSm('tw')} target="_blank" className="sm-ic-outer logo big-only" style={{backgroundColor: '#1DA1F2'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+twitter+')'}} />
					</a>
					<a href='https://www.instagram.com/thrillsofkerala/' onClick={() => openSm('in')} target="_blank" className="sm-ic-outer logo big-only" style={{backgroundColor: '#E1306C'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+instagram+')'}} />
					</a>
				</div>
			</div>
			<div className="footer">
				<div className="footer-sm-row small-only">
					{/* <div style={{marginRight: '5px'}}>Support us on:</div> */}
					<a href='https://www.facebook.com/thrillsofkerala/' onClick={() => openSm('fb')} target="_blank" className="sm-ic-outer logo" style={{backgroundColor: '#4267B2'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+facebook+')'}} />
					</a>
					<a href='https://twitter.com/thrillsofkerala/' onClick={() => openSm('tw')} target="_blank" className="sm-ic-outer logo" style={{backgroundColor: '#1DA1F2'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+twitter+')'}} />
					</a>
					<a href='https://www.instagram.com/thrillsofkerala/' onClick={() => openSm('in')} target="_blank" className="sm-ic-outer logo" style={{backgroundColor: '#E1306C'}}>
						<div className="sm-ic" style={{backgroundImage: 'url('+instagram+')'}} />
					</a>
					<div className="hdiv" />
					{['ml','en'].map((i,ii) => (
						<div onClick={() => changeLang(i)} key={ii} className={"lan "+(i!==lan?"in":"")+"activelan"}>{lang[i]}</div>
					))}
				</div>
				<div className="copyright">2021 © Thrills of Kerala</div>
			</div>
		</>
  );
}

export default App;
